
#app {
  width: 100%;
  height: 100%;
}

.login {
  width: 100%;
  height: 100%;
  background: url(/static/img/loginBg.bf3aa78.jpg) no-repeat 100% 100%;
  position: relative;
}
.login >.goto {
  width: 400px;
  height: 200px;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);
  background: rgba(255,255,255,0.3);
}
.login >.goto >input {
  display: block;
  width: 250px;
  height: 30px;
  border: none;
  outline: none;
  padding-left: 10px;
  border-radius: 4px;
  background: rgba(0,0,0,0.1);
  margin: 40px auto;
  color: #fff;
}
.login >.goto >button {
  display: block;
  width: 100px;
  height: 40px;
  background: rgba(199,122,58,0.6);
  border-radius: 10px;
  border: none;
  outline: none;
  cursor: pointer;
  color: #fff;
  margin: 0 auto;
}
.login >.down {
  width: 600px;
  height: 400px;
  border-radius: 10px;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -70%);
          transform: translate(-50%, -70%);
  background: rgba(255,255,255,0.3);
}
.login >.down >h1 {
  width: 100%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  color: #000;
}
.login >.down >h1 >span {
  color: #333;
}
.login >.down >.input {
  width: 400px;
  height: 40px;
  margin: 0 auto;
}
.login >.down >.input >input {
  width: 100%;
  height: 100%;
  border: none;
  padding-left: 10px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  border-radius: 4px;
}
.login >.down >.tps {
  width: 100%;
  height: 280px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 40px;
  padding-top: 60px;
  position: relative;
}
.login >.down >.tps >p {
  line-height: 24px;
}
.login >.down >.tps >.warning {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 30px;
  text-align: right;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 20px;
  color: #333;
}